    instagram_presidencia: this.datosBasicos.instagram_presidencia,
    snapchat_presidencia: this.datosBasicos.snapchat_presidencia,*/
    //Files Presidencia
    files_fotografia: [],
    files_fotografiaB64: this.datosBasicos.files_fotografia ? this.datosBasicos.files_fotografia : null,
    files_credencial: [],
    files_credencialB64: this.datosBasicos.files_credencial ? this.datosBasicos.files_credencial : null,
    files_plan_trabajo: [],
    files_plan_trabajoB64: this.datosBasicos.files_plan_trabajo ? this.datosBasicos.files_plan_trabajo : null,
    files_carta_no_violencia: [],
    files_carta_no_violenciaB64: this.datosBasicos.files_carta_no_violencia ? this.datosBasicos.files_carta_no_violencia : null,
    files_kardex: [],
    files_kardexB64: this.datosBasicos.files_kardex ? this.datosBasicos.files_kardex : null,
    files_planilla_urna_votacion: [],
    files_planilla_urna_votacionB64: this.datosBasicos.files_planilla_urna_votacion ? this.datosBasicos.files_planilla_urna_votacion : null,
    files_carta_militancia: [],
    files_carta_militanciaB64: this.datosBasicos.files_carta_militancia ? this.datosBasicos.files_carta_militancia : null,
    // nombre_alias_presidencia: this.datosBasicos.nombre_alias_presidencia,
    // apodo_presidencia: this.datosBasicos.apodo_presidencia,
    // equipo_presidente: this.datosBasicos.equipo_presidente,
