    // carrera_vicepresidencia: this.datosBasicos.carrera_vicepresidencia,
    /*turno_vicepresidencia: this.datosBasicos.turno_vicepresidencia,
    semestre_vicepresidencia: this.datosBasicos.semestre_vicepresidencia,
    codigo_vicepresidencia: this.datosBasicos.codigo_vicepresidencia,
    sexo_vicepresidencia: this.datosBasicos.sexo_vicepresidencia,
    genero_vicepresidencia: this.datosBasicos.genero_vicepresidencia,
    grupo_politico_vicepresidencia: this.datosBasicos.grupo_politico_vicepresidencia,*/
    //Vicepresidencia ejecutiva
    // vicepresidencia_ejecutiva: [],
    //Secretaria
    /*nombre_secretaria: this.datosBasicos.nombre_secretaria,*/
    // carrera_secretaria: this.datosBasicos.carrera_secretaria,
    /*turno_secretaria: this.datosBasicos.turno_secretaria,
    semestre_secretaria: this.datosBasicos.semestre_secretaria,
