    (v) => !!v || "Campo requerido",
    (v) => (v && v.length >= 2) || "Debe tener más de 2 caracteres",
    ],
    codigoUGRules: [
    (v) => !!v || "Código U de G es requerido",
    (v) =>
    (v && v.length >= 5) || "Código U de G debe tener más de 2 caracteres",
    (v) => /^\d*$/.test(v) || "Solo números",
    ],
    emailRules: [
    (v) => !!v || "Correo electrónico es requerido",
    (v) => /.+@.+/.test(v) || "Correo electrónico debe ser válido",
    ],
    selectRules: [(v) => !!v || "Campo requerido"],
  }
},
methods: {
  ...mapMutations(["ocultarLoad", "mostrarLoad"]),
  cerrarModalActual() {
    this.files_fotografia = []
    this.files_fotografiaB64 = null
    this.files_credencial = []
    this.files_credencialB64 = null
    this.files_plan_trabajo = []
    this.files_plan_trabajoB64 = null
    this.files_carta_no_violencia = []
    this.files_carta_no_violenciaB64 = null
    this.files_kardex = []
    this.files_kardexB64 = null
    this.files_planilla_urna_votacion = []
    this.files_planilla_urna_votacionB64 = null
    this.files_carta_militancia = []
    this.files_carta_militanciaB64 = null
    this.$emit(newFunction(), false)

    function newFunction() {
      return 'cerrarModalFormEdit'
    }
  },
      // LLamada a API
      async getSexo() {
        this.loading = true;
        let sexo = await axios.get(
          `${apiURL}obtainsex`
          );
        this.loading = false;
        let sx = sexo.data.data;
        Object.keys(sx).forEach((element) => {
        //console.log( sx[element].sexo);
        this.sexos.push(sx[element].sexo);
      });
      },
      async getGenero() {
        this.loading = true;
        let genero = await axios.get(
          `${apiURL}obtaingender`
          );
        this.loading = false;
        let gn = genero.data.data;
        Object.keys(gn).forEach((element) => {
        //console.log( gn[element].genero);
        this.generos.push(gn[element].genero);
      });
      },

      async getTurnos() {
        this.loading = true;
        let turno = await axios.get(
          `${apiURL}obtainturns`
          );
        this.loading = false;
        let tn = turno.data.data;
        Object.keys(tn).forEach((element) => {
        //console.log( gn[element].genero);
        this.turnos.push(tn[element].turno);
      });
      },

      async postObtenerCentrosUniversitarios() {
        this.loading = true;
        let respuesta = await axios.post(
          `${apiURL}obtaincentros`,
          {
            eleccion_id: this.eleccion_id,
          }
          );
        this.loading = false;
        let lista_centros = respuesta.data.data;
        Object.keys(lista_centros).forEach((element) => {
        //console.log( lista_niveles[element].nivel);
        this.centros.push({
          value: lista_centros[element].centro,
          label: lista_centros[element].centro_desc,
        });
      });
      //Se comprueba si es preparatoria
      if (this.datosBasicos.nivel_presidente === "Preparatoria") {
        this.centros = this.centros.filter(function (centro) {
          return centro.value === "SEMS";
        });
      } else {
        this.centros = this.centros.filter(function (centro) {
          return centro.value !== "SEMS";
        });
      }
    },

    async postObtenerDivisiones() {
      this.loading = true;
      this.divisiones = [];
      let respuesta = await axios.post(
        `${apiURL}obtaindivision`,
        {
          eleccion_id: this.eleccion_id,
          centro: this.centro_presidencia,
        }
        );
      this.loading = false;
      let lista_divisiones = respuesta.data.data;
      Object.keys(lista_divisiones).forEach((element) => {
        //console.log( lista_niveles[element].nivel);
        this.divisiones.push({
          value: lista_divisiones[element].plantel,
          label: lista_divisiones[element].plantel,
        });
      });
    },

    async getGruposPoliticos() {
      this.loading = true;
      let grupospoliticos = await axios.get(
        `${apiURL}obtainpoliticalgroups`
        );
      this.loading = false;
      let gp = grupospoliticos.data.data;
      Object.keys(gp).forEach((element) => {
        //console.log( gp[element]);
        this.grupos_politicos.push({
          value: gp[element].id,
          label: gp[element].nombre,
        });
      });
      this.grupos_politicos = this.grupos_politicos.sort(function (a, b) {
        if (a.label > b.label) {
          return 1;
        }
        if (a.label < b.label) {
          return -1;
        }
        // a must be equal to b
        return 0;
      });
    },

    async postObtenerCarreras() {
      this.loading = true;
      this.carreras = [];
      this.vicepresidencia_ejecutiva = [];
      let respuesta = await axios.post(
        `${apiURL}obtaincarrera`,
        {
          eleccion_id: this.eleccion_id,
          centro: this.centro_presidencia,
          plantel: this.division_presidencia,
        }
        );
      this.loading = false;

      let lista_carreras = respuesta.data.data;
      Object.keys(lista_carreras).forEach((element) => {
        //console.log( lista_niveles[element].nivel);
        this.vicepresidencia_ejecutiva.push({nombre: "",carrera: "",turno: "",semestre: "",codigo: "",sexo: "",genero: "",files_credencial: [],files_credencialB64: "",files_kardex: [],files_kardexB64: "",
      });
        this.carreras.push({
          value: lista_carreras[element].carrera,
          label: lista_carreras[element].carrera,
        });
      });
      this.carreras = this.carreras.sort(function (a, b) {
        if (a.label > b.label) {
          return 1;
        }
        if (a.label < b.label) {
          return -1;
        }
        // a must be equal to b
        return 0;
      });
    },
